import React from 'react';
import { connect } from 'react-redux';
import ListGroup from 'react-bootstrap/ListGroup';
import Button from 'react-bootstrap/Button';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus, faTrashAlt, faChevronRight } from '@fortawesome/free-solid-svg-icons';

import { changeActivePdf } from '../actions';

let PdfView = ({pdf, dispatch}) => {	
	return (
		<ListGroup.Item className={pdf.is_active ? "list-group-item-primary" : ""}>			
			<Button variant="link"  onClick={e => {
				e.preventDefault();	
				dispatch(changeActivePdf(pdf.index));
			}}><span>{pdf.path}</span> <FontAwesomeIcon icon={faChevronRight} /></Button>				
			<Button variant="link"  onClick={e => {
			e.preventDefault();	
			}}><FontAwesomeIcon icon={faTrashAlt} /></Button>
			{pdf.user ?  <div><small>(Loaded by {pdf.user.id == pdf.current_user.id ? "you" : pdf.user.name})</small></div> : <></>}
		</ListGroup.Item>
	);
}
PdfView = connect()(PdfView);
export default PdfView;